(function () {

    angular
        .module('meanApp')
        .service('authentication', authentication);

    authentication.$inject = ['$http', '$window','$location'];
    function authentication ($http, $window, $location) {

        var saveToken = function (token) {
            $window.localStorage['mean-token'] = token;
        };

        var getToken = function () {
            return $window.localStorage['mean-token'];
        };

        var isLoggedIn = function() {
            var token = getToken();
            var payload;

            if(token){
                payload = token.split('.')[1];
                payload = $window.atob(payload);
                payload = JSON.parse(payload);

                return payload.exp > Date.now() / 1000;
            } else {
                return false;
            }
        };

        var currentUser = function() {
            if(isLoggedIn()){
                var token = getToken();
                var payload = token.split('.')[1];
                payload = $window.atob(payload);
                payload = JSON.parse(payload);
                return {
                    email : payload.email,
                    name : payload.name,
                    role : payload.role,
                    id : payload._id
                };
            }
        };

        register = function(user) {

            return $http.post('/api/register', user).success(function(data){
                //saveToken(data.token);
            });
        };

        login = function(user) {
            return $http.post('/api/login', user).success(function(data) {
                saveToken(data.token);
                var token = data.token;
                var payload = token.split('.')[1];
                payload = $window.atob(payload);
                payload = JSON.parse(payload);
                console.log("ici role :" + payload.role)
                if(payload.role =='Admin'){
                    $location.path('admin/accueil')
                }else if(payload.role =='Responsable-Sallon'){

                    $location.path('admin/profilesalon/'+payload._id)
                }else if(payload.role =='Client'){

                    $location.path('/')
                }
            });
        };

        logout = function() {
            $window.localStorage.removeItem('mean-token');

            $window.location.reload()
            $location.path('/admin');
        };


        return {
            currentUser : currentUser,
            saveToken : saveToken,
            getToken : getToken,
            isLoggedIn : isLoggedIn,
            register : register,
            login : login,
            logout : logout,

        };
    }


})();
